import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useDispatch, useSelector } from 'react-redux';
import { searchSubTasksByLabelAction } from 'reducers/subTasksReducer/actions';
import { subTaskFiltersSelector } from 'reducers/subTasksReducer/selectors';
import { LabelsListContainer, LabelStyled, LabelListItem } from './LabelsList.styles';

// TODO: move to atoms
const Label = ({ label }) => {
  const filters = useSelector(subTaskFiltersSelector);
  const dispatch = useDispatch();
  const handleClick = useCallback(
    // TODO: move this filters concating to reducer
    // by splitting searchSubTasksByLabelAction action into setFilters and seach..ByLabel
    () => dispatch(searchSubTasksByLabelAction(new Set([...filters, label]))),
    [dispatch, searchSubTasksByLabelAction, label],
  );

  return (
    <LabelStyled className="label purple-button" onClick={handleClick}>
      #{label}
    </LabelStyled>
  );
};

Label.propTypes = {
  label: PropTypes.string.isRequired,
};

const LabelsList = ({ labels }) => (
  <LabelsListContainer>
    {labels.map(label => (
      <LabelListItem key={label}>
        <Label label={label} />
      </LabelListItem>
    ))}
  </LabelsListContainer>
);

LabelsList.defaultProps = {
  labels: [],
};

LabelsList.propTypes = {
  labels: PropTypes.arrayOf(PropTypes.string),
};

export default LabelsList;
